import React, {useState} from 'react'
import Affairs from './Affairs'

// types
export type AffairPriorityType = 'low' | 'middle' | 'high' // fixed
export type AffairType = {
    _id: number
    name: string
    priority: string
} //fixed
export type FilterType = 'all' | AffairPriorityType

// constants
const defaultAffairs: Array<AffairType> = [ // fixed
    {_id: 1, name: 'React', priority: 'high'},
    {_id: 2, name: 'anime', priority: 'low'},
    {_id: 3, name: 'games', priority: 'low'},
    {_id: 4, name: 'work', priority: 'high'},
    {_id: 5, name: 'html & css', priority: 'middle'},
]

// pure helper functions
export const filterAffairs = (affairs: Array<AffairType>, filter: string): Array<AffairType> => { // fixed
    if (filter === 'all') return affairs
    if (filter === 'low') return affairs.filter(l => l.priority === 'low')
    if (filter === 'middle') return affairs.filter(l => l.priority === 'middle')
    if (filter === 'high') return affairs.filter(l => l.priority === 'high')
    else return affairs // fixed
}
export const deleteAffair = (affairs: Array<AffairType>, _id: number): Array<AffairType> => { // fixed
    return affairs.filter(id => id._id !== _id) // fixed
}

function HW2() {
    const [affairs, setAffairs] = useState<Array<AffairType>>(defaultAffairs) // fixed
    const [filter, setFilter] = useState<FilterType>('all')

    const filteredAffairs = filterAffairs(affairs, filter)
    const deleteAffairCallback = (_id: number) => setAffairs(deleteAffair(affairs, _id)) // fixed

    return (
        <div>
            <hr/>
            homework 2:

            {/*should work (должно работать)*/}
            <Affairs
                data={filteredAffairs}
                setFilter={setFilter}
                deleteAffairCallback={deleteAffairCallback}
                filter={filter}
            />

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeAffairs/>*/}
            <hr/>
        </div>
    )
}

export default HW2
